#root {
    display: flex;
    flex-direction: column;
    height: 100vh;
    font-family: sans-serif;
}

/* App-Header */
div .header-navbar {
    z-index: 1;
    position: fixed;
    background-color: #1C1211;
    height: 8vh;
    width: 100vw;
}

.header-link {
    font-family: Brush Script MT, Brush Script Std, cursive;
    font-weight: 400;
    font-size: 190%;
    color: #B06B32;
    margin: 0 10px;
}
.header-link:first-child {
    margin-left: 40px;
}
.header-link:hover {
    color: #1C7B42;
    text-decoration: none;
}

.header-cart-link > .cart-total {
    height: inherit;
}
.cart-img {
    height: 5vh;
}


/* Burger */
.navbar-toggler {
    margin-top: -10px;
}
.navbar-collapse {
    
    float:right;
    background-color:#1C1211;
    width: 100%;
}
.navbar-collapse > div {
    align-items: center;
}


/* Carousel */

.main-carousel img {
    width: 100vw;
}

/* Catalog-page */
.catalog-page {
    margin-top: 8vh;
}

.catalog-category {
    color: #B06B32;
    position: fixed;
    min-width: 15vw;
    left: 5vh;
}
.catalog-badge {
    position: absolute;
    right: 3px;
    background-color: #B06B32;
    color: #1C1211;
}
.col-category {
    display: flex;
    margin-top: 5vh;
    min-height: 50vh;
}

ul > .catalog-item{
    background-color: #1C1211;
}

ul > .catalog-item:hover {
    background-color: #B06B32;
    color: #1C1211;
}

ul > .catalog-item:hover .catalog-badge {
    background-color: #1C1211;
    color: #B06B32;
}


/* Cards */

.item-wrapper {
    position: relative;
    border: 4px solid #B06B32;
    border-radius: 15px;
    width: 25vh;
    height: 30%;
    display: inline-block;
    margin: 20px 10px 16px 40px;
}
.item-img {
    position: relative;
    display: inline-block;
    border-radius: 15px;
    width: 100%;
    height: 180px;
}

.item-title {
    height: 3em;
    text-align: center;
    font-family: sans-serif;
    font-weight: 600;
    margin: 2px 0 10px;
    border-bottom: 1px solid #B06B32;
    border-top: 1px solid #B06B32;
}

.item-wrapper > button {
    width: 100%;
    background-color: #1C1211;
    color: #B06B32;
    border-radius: 15px;
}

.item-wrapper > button:hover {
    background-color: #B06B32;
    color: #1C1211;
}

/* Contacts */
.contact-wrapper {
    margin-top: 10vh;
    margin-bottom: 4%;
}

.contacts-geo {
    height: 20vh;
}

.contacts-map {
    height: 20vh;
}

.contacts-phone {
    margin: 6em 0;
    height: 4em;
}

.contacts-callback {
    width: 100%;
    height: 3em;
    background-color: #1C1211;
    color: #B06B32;
    border-radius: 15px;
}

.contacts-callback:hover {
    background-color: #B06B32;
    color: #1C1211;
}

.contacts-img {
    height: 2.5em;
}

.contacts-soc {
    display: block;
    margin: 0 auto;
}

.contacts-soc > a {
    margin-left: 10px;
    margin-right: 10px;
}

.contacts-soc > a:hover {
    opacity: 0.5;
}

/* Cart */

.empty-cart {
    text-align: center;
    border: 5px double #B06B32;
    border-radius: 10px;
    background-color: #1C1211;
    color: #B06B32;
    font-size: 20px;
    font-weight: 600;
    margin: 8vh 30%;
}
.empty-cart:hover {
    background-color:#B06B32;
    border-color: #1C1211;
    color: #1C1211;
}

.cart-item {
    position: relative;
    display: flex;
    width: 70vw;
    justify-content: space-between;
    align-items: center;
    border: 3px solid #1C1211;
    border-radius: 10px;
    margin: 40px auto 0 auto;
    background-color: #B06B32;
    box-shadow: 0 0 20px rgba(256,256,256, .1);
}

.cart-item-close {
    position: absolute;
    text-align: center;
    width: 25px;
    height: 25px;
    top: 5px;
    right: 5px;
    color: #fff;
    border-radius: 100%;
    border: 1px solid #fff;
    cursor: pointer;
}

.cart-item > img {
    display: flex;
    height: 125px;
    width: 80%;
    border-right: 5px solid #1C1211;
    margin: 0;
    border-radius: 10px;
    object-fit: cover;
}

.cart-buttons {
    display: flex;
}
.cart-buttons > button {
    width: 30px;
    height: 30px;
    background-color: #B06B32;
    border: 2px solid #1C1211;
    border-radius: 100%;
}
.cart-buttons > button:hover {
    background-color: #1C7B42;
}

.cart-purchase {
    width: 10em;
    height: 3em;
    display: block;
    margin: 2em auto;
    border: 2px solid #1C7B42;
    border-radius: 3px;
    background-color: #1C1211;
    color: #1C7B42;
}

.cart-purchase:hover {
    border-color: #1C1211;
    background-color: #B06B32;
    color: #1C1211;
}

/* Footer */

.footer-navbar {
    background-color: #1C1211;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 100%;
    flex-grow: 1;
}
.footer-block{
    position: relative;
    display: flex;
    float: left;
}
.footer-block > a {
    margin: 0 1em;
    color: #B06B32;
}
.footer-block > a:last-child {
    margin-top: 0.5em;
}
.footer-block > a:hover {
    text-decoration: none;
    color: #1C7B42;
}
.footer-block > img {
    margin-top: 7px;
    height: 2em;
}
.footer-soc{
    position: relative;
}

.footer-soc > a > img {
    float: right;
    height: 3em;
    margin-top: 3px;
    margin-right: 3px;
}
.footer-soc > a:hover {
    opacity: 0.5;
}